
    
.portfolio_bg{
    position: relative;
    z-index: 20;
    height: 100%;
    width: 100%;
    margin-bottom: 10%;
    transition: initial;
    .portfolio_bg_img{
        position: fixed;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: -1;
        }
.portfolio_bg_wrapper{
    h2{
        margin: 0 auto;
        padding: 0px 0 500px 0;
        text-align: center;
        font-size: 14vw;
        color: #f5f0ec;
        font-weight: 700;
        text-rendering: geometricPrecision;
        letter-spacing: 8px;
        padding: 400px 0;
    }
    .portfolio_bg_button{
        z-index: 12;
        position: relative;
        padding: 20px 30px;
        font-size: 2vw;
        background-color: #285efe;
        color: #000;
        margin: 50px auto 0;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 40px;
        font-family: Arial;
        transition: .3s ease-in-out;
        &:hover{
          box-shadow: 0px 0px 5px #f4f2f2;
        }
        &:hover img{
          transform: translateY(-3px);
        }
        @media (max-width:800px){
          font-size: 20px;
        }
        a{
          color: #fff;
          display: flex;
          align-items: center;
          justify-content: space-between;
          img{
            display: block;
            width: 30px;
            height: 40px;
            margin-left: 20px;
            rotate: 90deg;
            transition: .3s ease-in-out;
          }
        }
    }
    .portfolio_bg_grid{
      width: 100%;
      height: 100%;
      margin-top: -200px;
      display: grid;
      .portfolio_bg_grid_1{
        display: flex;
        justify-content: flex-end;
        z-index: 2;
        img{
          object-fit: cover;
          width: 70%;
          @media (max-width:1100px){
            height: 800px;
            width: 60%;
          }
          @media (max-width:800px){
            width: 80%;
          }
        }
      }
      .portfolio_bg_grid_2{
        width: 100%;
        height: 100%;
        margin-top: -5%;
        z-index: 1;
        img{
          width: 90%;
          object-fit: cover;
        }
      }
      .portfolio_bg_grid_3{
        width: 100%;
        height: 100%;
        margin-top: 5%;        
        display: flex;
        justify-content: center;
        z-index: 2;
        img{
          width: 55%;
          object-fit: cover;
          @media (max-width:1100px){
            height: 800px;
            width: 60%;
          }
          @media (max-width:800px){
            width: 80%;
          }
          @media (max-width:510px){
            width: 90%;
          }
        }
      }
      .portfolio_bg_grid_4{
        width: 100%;
        height: 100%;
        margin-top: -5%; 
        display: flex;
        z-index: 1;
        img{
          width: 55%;
          object-fit: cover;
        }
        
      }
      .portfolio_bg_grid_5{
        width: 100%;
        height: 100%;
        margin-top: 5%; 
        display: flex;
        justify-content: flex-end;
        z-index: 2;
        img{
          width: 60%;
          object-fit: cover;
        }
      }
      .portfolio_bg_grid_6{
        margin-top: -7%; 
        margin-left: 2%;
        z-index: 1;
        img{
          width: 80%;
          object-fit: cover;
        }
      }
      .portfolio_bg_grid_7{
        margin-top: 5%;
        display: flex;
        justify-content: flex-end;
        z-index: 2;
        img{
          width: 80%;
          object-fit: cover;
        }
      }
      .portfolio_bg_grid_8{
        margin-top: -5%;
        margin-left: 2%;
        z-index: 1;
        
        img{
          width: 50%;
          pointer-events: all;
          object-fit: cover;  
          @media (max-width:1100px){
            width: 60%;
          }
          @media (max-width:800px){
            width: 80%;
          }
          @media (max-width:510px){
            width: 90%;
          }
        }
      }
    }
  }
}
